.counter {
  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &:not(:last-child) {
      margin: 0 0 30px;
    }

    &_detail {
      margin: 0;
      width: 280px;
    }
  }

  &-title {
    @include fz(16px);
    margin: 0;
    color: $c-dark;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }

  &-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 120px;

    &__btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 24px;
      height: 24px;
      padding: 0;
      background-color: #F3F3F3;
      border-radius: 50%;
      border: none;
      cursor: pointer;
      outline: none;

      &:focus {
        box-shadow: 0 0 1px 2px rgba(8, 57, 111, .6);
      }

      &:hover {
        background-color: darken(#F3F3F3, 5%);
      }
    }

    &__sum {
      @include fz(16px);
      min-width: 32px;
      color: $c-dark;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
      text-align: center;
    }
  }
}