.team-banner {
  margin: 0 -20px 80px;
  padding: 30px 20px 80px;
  background-color: $c-blue;

  @include mq(tablet) {
    margin: 0 0 80px;
    padding: 34px 30px 60px;
    border-radius: 10px;
  }

  @include mq(desktop) {
    padding: 60px 80px;
  }

  &_main {
    padding: 408px 30px 60px;
    margin: -408px -20px 0;
    border-radius: 0;

    @include mq(tablet) {
      padding: 470px 30px 60px;
      margin: -470px -20px 0;
      border-radius: 0;
    }

    @include mq(desktop) {
      padding: 190px 80px 60px;
      margin: -190px 0 80px;
      background-image: url("../../../img/index/team.png");
      background-repeat: no-repeat;
      background-position: right -22px bottom -112px;
      background-size: cover;
      border-radius: 10px;

      @include retina() {
        background-image: url("../../../img/index/team@2x.png");
      }
    }
  }
}