.check-box {
  &__field {
    position: absolute;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    box-shadow: none;

    &[type="radio"] + .check-box__label span {
      position: absolute;
      top: -2px;
      left: 0;
      width: 16px;
      height: 16px;
      background-color: #e6ecf7;
      border-radius: 50%;

      @include mq(tablet) {
        top: 0;
      }
    }

    &[type="radio"]:checked + .check-box__label {
      color: $c-blue;
      font-family: $f-muller-bold;

      & span::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background: linear-gradient(45deg, #3c589e, #93b7e4);
        transform: translate(-50%, -50%);
      }
    }

    &[type="radio"]:focus + .check-box__label span {
      box-shadow: 0 0 1px 2px rgba(8, 57, 111, .6);
    }

    &[type="checkbox"] + .check-box__label span {
      position: absolute;
      top: 1px;
      left: 0;
      width: 14px;
      height: 14px;
      background-color: #e6ecf7;
      border-radius: 2px;
    }

    &[type="checkbox"]:checked + .check-box__label {
      color: $c-blue;
      font-family: $f-muller-bold;

      & span::after {
        content: "";
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 2px;
        background: linear-gradient(45deg, #3c589e, #93b7e4);
      }

      & span::before {
        content: "";
        position: absolute;
        z-index: 3;
        top: 50%;
        left: 50%;
        width: 6px;
        height: 5px;
        background: url("../../img/catalog/check.svg") no-repeat center;
        transform: translate(-50%, -50%);
      }
    }

    &[type="checkbox"]:focus + .check-box__label span {
      box-shadow: 0 0 1px 2px rgba(8, 57, 111, .6);
    }
  }

  &__label {
    @include fz(15px);
    position: relative;
    padding-left: 26px;
    color: $c-dark;
    font-family: $f-muller-medium;
    letter-spacing: .3px;

    @include mq(tablet) {
      @include fz(18px);
      cursor: pointer;
    }
  }
}