.button {
  @include button;

  &_filter {
    margin: 0 0 30px;
    text-decoration: none;

    @include mq(tablet) {
      width: 220px;
      margin: 0;
    }

    @include mq(desktop) {
      display: none;
    }
  }

  &-service {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 0 30px;
    height: 50px;
    border-radius: 10px;
    text-decoration: none;
    user-select: none;
    transition: box-shadow .3s;

    @include mq(tablet) {
      width: 220px;
      margin: 0;
    }

    @include mq(desktop) {
      width: 280px;
    }

    &__icon {
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 20px 0 0;
      width: 24px;
      height: 24px;

      svg {
        width: 100%;
        height: 100%;
        fill: $c-white;
      }
    }

    &__text {
      position: relative;
      top: 2px;
      z-index: 2;
      font-size: 0.9375rem;
      color: #ffffff;
      font-family: "MullerMedium", sans-serif;
    }

    &_red {
      background: linear-gradient(45deg, #9B0006, #D30020);

      &:hover, &.active {
        box-shadow: 0 5px 40px rgba(208, 0, 31, .55);
      }

      &:active {
        box-shadow: none;
      }

      .button-service__icon {
        @include mq(desktop, max) {
          display: none;
        }
      }
    }

    &_blue {
      background: #405AA0;

      &:hover, &.active {
        box-shadow: 0 5px 40px rgba(64, 90, 160, .55);
      }

      &:active {
        box-shadow: none;
      }

      .button-service__icon {
        @include mq(desktop, max) {
          display: none;
        }
      }
    }

    &_light-blue {
      background: #94B7E4;

      &:hover, &.active {
        box-shadow: 0 5px 40px rgba(148, 183, 228, .55);
      }

      &:active {
        box-shadow: none;
      }

      .button-service__icon {
        @include mq(desktop, max) {
          display: none;
        }
      }
    }

    &_grey {
      background: #F3F3F3;

      @include mq(desktop) {
        display: none;
      }

      .button-service__icon {
        display: block;
      }

      .button-service__text {
        color: #626262;
      }
    }
  }

  &_grey {
    position: absolute;
    right: 0;
    bottom: 0;

    @include mq(desktop, max) {
      right: 20px;
    }

    &::before {
      background: #F3F3F3;
    }

    .button__shadow::after {
      box-shadow: none;
    }

    .button__text {
      color: #626262;
    }

    &:active,
    &:hover::before {
      background: #ebebeb;
    }
  }
}
