.team {
  position: relative;
  padding: 0 20px 30px;
  margin: 0 15px 40px;
  border-radius: 10px;

  &::after {
    content: '';
    position: absolute;
    top: 60px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $c-white;
    border-radius: 10px;

    @include mq(tablet) {
      top: 70px;
    }

    @include mq(desktop) {
      top: 100px;
    }
  }

  @include mq(tablet) {
    padding: 0 28px 30px;
  }

  &_main {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    width: calc(50% - 30px);
    margin: 0 15px 40px;

    @include mq(tablet) {
      width: calc(33% - 50px);
      margin: 0 25px 50px;
    }

    @include mq(desktop) {
      width: calc(25% - 60px);
      margin: 0 30px 60px;
    }
  }

  &_detail {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    padding: 0;
    margin: 0 0 30px;

    @include mq(tablet) {
      padding: 0;
    }
  }
}
