.star {
  display: flex;
  align-items: center;

  @include mq(desktop) {
    margin: 0 20px 0 0;
  }

  &__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 10px;
    height: 10px;

    &:not(:last-child) {
      margin: 0 6px 0 0;
    }

    svg {
      width: 100%;
      height: 100%;
    }

    &_detail {
      width: 16px;
      height: 16px;
    }
  }
}