html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

html, body {
  height: 100%;
  font-family: $f-din-bold;
}

body {
  padding: 0;
  margin: 0;
  line-height: 1.25;
  background-color: $c-main-bg;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.container {
  width: 100%;
  padding: 0 20px;

  @include mq(tablet) {
    max-width: 768px;
    margin: 0 auto;
  }

  @include mq(desktop) {
    width: 90%;
    max-width: 1300px;
    padding: 0;
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.header, .footer {
  flex: 0 0 auto;
}

.main {
  flex: 1 0 auto;
  margin: 160px 0 0;

  &_bg {
    margin: 0;
    background-image: url("../../img/index/bg.jpg");
    background-repeat: no-repeat;
    background-position: center bottom -235px;
    background-size: unset;
  }
}

.center {
  margin: auto;
}

.m-hidden {
  @include mq(tablet, max) {
    display: none;
  }
}

.t-hidden {
  @include mq(desktop, max) {
    display: none;
  }
}

.d-hidden {
  @include mq(tablet) {
    display: none;
  }
}

.white-logo {
  display: none;
}

.no-js, .isOpen {
  .adaptive-list__item_btn-open {
    display: none;
  }

  .adaptive-list__item_btn-close {
    display: block;
  }

  .blue-logo {
    display: none;
  }

  .white-logo {
    display: block;
    margin: auto;

    @include mq(tablet) {
      transform: translateX(50%);
    }
  }

  @include mq(desktop, max) {
    & .header {
      background-color: $c-blue;

      &_main {
        transition: all .3s ease;
      }
    }

    & .nav-list__link, & .nav-dropdown__link {
      color: $c-white;
    }

    & .nav-dropdown__icon {
      stroke: $c-white;
    }

    & .nav-dropdown__icon, & .nav-compare__icon {
      fill: $c-white;
    }

    & .nav__inner {
      @include mq(desktop) {
        display: flex;
      }
    }
  }
}

.no-js {
  .header {
    position: static;
  }
}

.isOpen {
  & .wrapper {
    position: fixed;
  }

  & .nav__inner {
    top: 100px;
    opacity: 1;

    @include mq(tabler, max) {
      padding: 50px 20px 0;
    }

    @include mq(desktop, max) {
      padding: 100px 20px 0;
    }
  }

  & .nav-compare__icon svg {
    fill: $c-white;
  }
}

.line-wrap {
  position: relative;
}
