.title {
  padding-top: 250px;
  max-width: 510px;

  h1 {
    margin: 0 0 50px;
    @include fz(80px);
    font-family: $f-din-bold;
    color: $c-white;
    letter-spacing: .3px;
  }

  p {
    margin: 0 0 40px;
    @include fz(18px);
    font-family: $f-muller-medium;
    color: rgba(255, 255, 255, .7);
    letter-spacing: .3px;
  }

  &__icon-wrap {
    display: flex;
    align-items: center;

    span {
      @include fz(12px);
      font-family: $f-muller-medium;
      color: rgba(255, 255, 255, .5);
      letter-spacing: .3px;
    }
  }

  &__icon {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    margin: 0 10px 0 0;
    box-shadow: 0 3px 10px rgba(132, 164, 215, .55);
  }
}

.main-info {
  position: absolute;
  bottom: 100px;
  height: 80px;
  width: 100%;
  max-width: 1300px;
  background-color: $c-white;
  border-radius: 10px;
}