.datepicker {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &_detail {
    width: 280px;
  }

  &__row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0 0 10px;
    border-bottom: 2px solid rgba(187, 187, 187, 0.5);
  }

  &__field {
    @include fz(16px);
    display: block;
    width: 105px;
    color: $c-dark;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
}
