.pagination {
  @include mq(tablet) {
    margin: 0 0 60px;
  }

  &-list {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;

    &__item {
      display: flex;
      margin: 0 2px;

      &:first-child {
        justify-content: flex-start;
        flex-grow: 1;
      }

      &:last-child {
        justify-content: flex-end;
        flex-grow: 1;
      }
    }
  }
  
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 20px;
    @include fz(16px);
    color: #B9B9B9;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    text-decoration: none;

    &:not(.active):hover {
      color: $c-link-hover;
    }

    &.active {
      color: $c-blue;
    }
  }
}