.footer-head {
  margin: 0 0 18px;

  &_main {
    position: relative;
    padding: 0 70px 0 0;

    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: 4px;
      width: 58px;
      height: 44px;
      background: url("../../../img/golden.png") no-repeat center;
    }

    @include mq(tablet, max) {
      max-width: 340px;
    }

    @include mq(tablet) {
      width: 50%;
    }

    @include mq(desktop) {
      width: auto;
    }
  }
}
