.review {
  position: relative;
  padding: 0 20px 30px;
  border-radius: 10px;

  @include mq(tablet) {
    padding: 0 30px 30px;
  }

  &::after {
    content: '';
    position: absolute;
    top: 40px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $c-white;
    border-radius: 10px;
  }

  &__subtitle {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    margin: 0 0 16px;
  }

  &__icon-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 13px;
    height: 16px;
    margin: 0 14px 0 0;
  }

  &__text {
    @include fz(14px);
    color: $c-field-default;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}