.boat {
  padding: 16px 18px 18px;
  text-decoration: none;
  border-radius: 10px;
  background-color: $c-white;

  @include mq(tablet) {
    padding: 24px 24px 16px;
  }
  
  &:hover {
    background-color: $c-blue;
    
    .boat__name {
      color: $c-white;
    }

    @include mq(tablet) {
      .boat__info {
        background-color: $c-blue-shadow;
      }
    }

    .boat__icon {
      fill: $c-white;
    }

    .boat__count {
      color: $c-white;
    }

    .boat__arrow svg {
      fill: $c-white;
    }
  }

  &__img {
    position: relative;
    height: 178px;

    picture {
      position: absolute;
      right: 0;
      left: 0;
      bottom: 20px;
    }

    img {
      margin: auto;
    }
  }

  &__arrow {
    margin: 16px 22px 6px;

    svg {
      fill: $c-blue
    }
  }

  &-slider {
    max-width: 1300px;
    margin: 0 auto;

    .slick-list {
      @media screen and (min-width: 768px) and (max-width: 991px) {
        padding: 100px 12% 0 !important;
      }

      @media screen and (min-width: 992px) and (max-width: 1199px) {
        padding: 100px 20% 0 !important;
      }

      @media screen and (max-width: 991px) {
        padding: 100px 70px 0 !important;
      }

      padding: 60px 0 0;
    }
  }
}
