.inspiration {
  position: relative;

  .badge {
    @media screen and (min-width: 768px) and (max-width: 1199px) {
      width: 130px;
    }
  }

  &-info {
    margin: 0 0 50px;

    p {
      @include fz(16px);
      margin: 0;
      color: $c-dark;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
      line-height: 23px;
    }
  }
}