.catalog {
  display: flex;
  flex-direction: column;

  @include mq(desktop) {
    flex-direction: row;
  }

  &-mb {
    margin: 0 0 28px;
    display: none;

    @media screen and (min-width: 768px) and (max-width: 1199px) {
      position: absolute;
      z-index: 3;
      width: 216px;
      margin: 0;
      padding: 20px 10px 20px;
      border-radius: 10px;
      background: $c-white;
      box-shadow: 0 20px 45px rgba(0, 0, 0, 0.2);
    }

    @include mq(desktop) {
      margin: 0 0 28px;
      display: block;
    }

    &_popup {
      display: block;

      @media screen and (min-width: 768px) and (max-width: 1199px) {
        position: static;
        width: 100%;
        border-radius: 0;
        box-shadow: none;
      }
    }
  }

  &__col {
    &:first-child {
      width: 100%;

      @include mq(desktop) {
        flex-shrink: 0;
        width: 280px;
      }
    }

    &:last-child {
      width: 100%;

      @include mq(desktop) {
        flex-grow: 1;
        padding-left: 60px;
      }
    }
  }

  &-sort {
    padding: 30px 0;

    @include mq(tablet) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 60px 0;
    }
  }

  &-available {
    display: none;

    @include mq(desktop) {
      @include fz(18px);
      display: block;
      color: $c-dark;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
    }
  }

  &-group-sort {
    display: flex;
    align-items: baseline;

    @include mq(tablet, max) {
      width: 100%;
      margin: 0 0 30px;
    }


    @include mq(desktop, max) {
      align-items: center;
      justify-content: center;
      height: 50px;
      width: 220px;
      background: #F3F3F3;
      border-radius: 10px;
    }

    @include mq(desktop) {
      width: 200px;
    }

    &__head {
      @include fz(15px);
      color: $c-blue;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
    }
  }

  &-service {
    @include mq(tablet) {
      display: flex;
      justify-content: space-between;
    }

    button.button {
      width: 280px;
    }
  }

  &-map {
    position: relative;
    display: block;
    height: 0;
    margin: 0 0 30px;
    border-radius: 10px;
    background: url("../../img/catalog/map@2x.jpg") center;
    background-size: cover;
    overflow: hidden;
    transition: height .3s;

    @include mq(desktop) {
      height: 120px;
      margin: 0 0 60px;
    }

    &__btn {
      display: none;

      @include mq(desktop) {
        position: absolute;
        top: 30px;
        right: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background: linear-gradient(45deg, #3C589E, #93B7E4);
        border: none;
        cursor: pointer;
      }

      &:hover {
        background: linear-gradient(45deg, #4D6FB8, #AACFEF);
      }
    }

    &.open {
      height: 460px;
    }
  }
}