.footer-info {
  &_main {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px 0 0;

    @include mq(desktop) {
      flex-grow: 1;
      padding: 70px 0 0;
    }
  }

  &__text {
    @include fz(12px);
    margin: 0;
    color: rgba(255, 255, 255, .5);
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    text-decoration: none;

    &_link {
      color: rgba(255, 255, 255, .7);

      &:hover {
        color: $c-white;
      }
    }
  }
}
