.slider {
  &-dots {
    .slick-list {
      margin: 0 0 80px;
    }

    .slick-dots {
      display: flex;
      padding: 0 0 25px;
      margin: 0;
      list-style: none;

      @include mq(desktop, max) {
        padding: 0 20px;
      }

      li {
        width: 12px;
        height: 7px;
        background: #E9E9E9;
        border-radius: 4px;
        cursor: pointer;

        & + li {
          margin-left: 8px;
        }

        &.slick-active {
          width: 64px;
          background: linear-gradient(45deg, #3C589E, #93B7E4);
        }
      }

      button {
        font-size: 0;
        padding: 0;
        margin: 0;
        background-color: transparent;
        border: none;
      }
    }

    &_countries {
      @media screen and (max-width: 540px) {
        margin: 0 20px;
      }

      .slick-list {
        margin: 0;
      }

      .slick-dots {
        position: absolute;
        left: 50%;
        bottom: -25px;
        padding: 0;
        transform: translateX(-50%);

        @include mq(tablet) {
          padding: 0;
          bottom: -70px;
        }

        @include mq(desktop) {
          padding: 0;
          bottom: -70px;
        }
      }
    }
  }

  &-wrap {
    position: relative;
    margin: 0 auto;
    max-width: 1300px;
    padding: 0 0 40px;

    @include mq(tablet) {
      padding: 0 0 100px;
      margin: 0 auto 50px;
    }

    @include mq(desktop) {
      padding: 0;
    }

    .route:not(:last-child) {
      margin: 0;

      @media screen and (max-width: 540px) {
        margin: 0 0 50px;
      }
    }

    .circle {
      position: absolute;
      z-index: 3;


      &_prev {
        @include mq(desktop, max) {
          left: 20px;
          bottom: 0;
        }

        @include mq(desktop) {
          left: 0;
          top: 50%;
          transform: translateY(-50%);
        }
      }

      &_next {
        @include mq(desktop, max) {
          right: 20px;
          bottom: 0;
        }

        @include mq(desktop) {
          right: 0;
          top: 50%;
          transform: translateY(-50%);
        }
      }
    }
  }

  &-best {
    max-width: 1300px;
    margin: auto;

    .slick-track {
      display: flex;
      padding: 10px 0 0;
    }

    .slick-center {
      @include mq(desktop, max) {
        box-shadow: 0 10px 30px rgba(0, 0, 0, 0.07);
      }
    }
  }

  &-countries {
    @include mq(desktop) {
      padding: 0 140px;
    }

    .route__img {
      @include mq(tablet, max) {
        margin: 0 14px 0 0;
        width: 60px;
      }
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;
    padding: 0 0 10px;
    margin: 0 0 30px;
    border-bottom: 2px solid rgba(187, 187, 187, .5);
  }

  &__field {
    @include fz(16px);
    max-width: 100px;
    color: $c-dark;
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    border: none;
    outline: none;

    &.right {
      text-align: right;
    }
  }

  &__icon {
    width: 30px;

    svg {
      width: 100%;
      height: 100%;
      fill: #08396f;
    }
  }

  .ui-slider {
    width: calc(100% - 18px);
    height: 6px;
    margin: auto;
    background: #E9E9E9;
    border-radius: 4px;
    border: none;

    .ui-slider-range {
      background: linear-gradient(45deg, #3c589e, #93b7e4);
    }
  }

  .ui-slider-handle {
    z-index: 1;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    border: 4px solid $c-white;
    box-shadow: 0 0 5px 5px rgba(139, 173, 220, .4);
    background: linear-gradient(45deg, #3c589e, #93b7e4);
    cursor: pointer;
  }
}