.detail {
  display: flex;
  justify-content: space-between;

  &__col {
    &:first-child {
      width: 960px;
    }

    &:last-child {
      width: 280px;
    }
  }

  &-sticky {
    position: sticky;
    top: 100px;
  }

  &-table {
    &-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 280px;

      &_full {
        width: 100%;
        justify-content: space-between;
      }
    }

    &__row {
      display: flex;
      justify-content: space-between;
      padding: 10px 0;

      &:not(:last-child) {
        border-bottom: 1px solid $c-border;
      }

      &:not(:last-child) {
        border-bottom: 1px solid $c-border;
      }

      span {
        @include fz(16px);
        color: $c-dark;
        font-family: $f-muller-medium;
        letter-spacing: .3px;
        width: 25%;

        &:nth-child(1) {
          width: 50%;
        }

        &:nth-child(3) {
          text-align: right;
        }
      }

      .red {
        color: $c-red;
      }

      .blue {
        color: $c-blue;
      }
    }
  }

  &-price {
    padding: 60px 86px;
    background-color: $c-blue;
    border-radius: 10px;

    &__row {
      display: flex;
      justify-content: space-between;

      &:not(:last-child) {
        margin: 0 0 25px;
      }
    }

    &-date {
      display: flex;

      &__icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 24px;
        height: 24px;
        margin: 0 20px 0 0;
      }
    }

    span {
      @include fz(16px);
      color: rgba(255, 255, 255, .5);
      font-family: $f-muller-medium;
      letter-spacing: .3px;

      &.small {
        @include fz(12px);
      }
    }

    p {
      margin: 0;
      @include fz(22px);
      color: rgba(255, 255, 255, 1);
      font-family: $f-muller-medium;
      letter-spacing: .3px;
    }

    &-info {
      display: flex;
      align-items: center;

      span {
        position: relative;
        top: 2px;
        margin: 0 0 0 10px;
      }
    }
  }

  &-form {
    border-radius: 10px;
    background-color: $c-white;
    overflow: hidden;

    &__head {
      padding: 10px 0 10px 80px;
      background-color: $c-blue;

      h4 {
        display: flex;
        align-items: center;
        margin: 0;

        span {
          margin: 0 0 0 20px;
          @include fz(20px);
          font-family: $f-din-bold;
          color: $c-white;
          letter-spacing: .3px;
        }
      }
    }

    &__body {
      padding: 40px 80px;
    }

    &__footer {
      padding: 0 80px;
    }

    &__row {
      display: flex;
      margin: 0 0 40px;

      &_border {
        border-top: 2px solid $c-border;
        margin: 20px 0 40px;
        padding: 20px 0 0;
      }
    }

    &__label {
      display: flex;
      flex-shrink: 0;
      width: 200px;
      @include fz(16px);
      color: $c-blue;
      font-family: $f-muller-medium;
      letter-spacing: .3px;

      svg {
        margin: 0 20px 0 0;
      }
    }
  }

  &-select {
    padding: 40px 20px;
    margin: 0 0 60px;
    text-align: center;
    border-radius: 10px;
    background-color: $c-white;

    &.wheel {
      background-image: url("../../img/catalog/wheel.png");
      background-repeat: no-repeat;
      background-position: right bottom;
      background-size: 90%;

      @include retina() {
        background-image: url("../../img/catalog/wheel@2x.png");
      }
    }

    h3 {
      @include fz(30px);
      margin: 0 0 20px;
      color: $c-blue;
      font-family: $f-din-bold;
      letter-spacing: .3px;
    }

    p {
      @include fz(16px);
      margin: 0 0 20px;
      color: $c-dark;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
    }

    & > span {
      @include fz(14px);
      display: inline-block;
      margin: 0 0 20px;
      color: $c-blue;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
    }
  }
}

.similar-slider {
  margin: 0 -30px;

  .slick-list {
    padding: 0 0 35px;
  }
}

.similar-slider .card {
  margin: 0 30px;

  &:hover {
    box-shadow: 0 15px 25px rgba(0, 0, 0, 0.1);
  }
}

textarea {
  width: 100%;
  height: 120px;
  padding: 20px 30px;
  border: 2px solid $c-border;
  border-radius: 10px;
  resize: none;
  @include fz(16px);
  color: $c-dark;
  font-family: $f-muller-medium;
  letter-spacing: .3px;
}