.card {
  position: relative;
  width: calc(100% - 30px);
  margin: 0 15px 30px;
  background-color: $c-white;
  border-radius: 10px;
  overflow: hidden;
  transition: all .3s;

  @include mq(tablet) {
    width: calc(50% - 50px);
    margin: 0 25px 50px;
  }

  @include mq(desktop) {
    width: calc(50% - 60px);
    margin: 0 30px 60px;
  }

  &:hover {
    box-shadow: 0 15px 55px rgba(0, 0, 0, .1);
  }

  &-favorite {
    position: absolute;
    z-index: 1;
    top: 30px;
    left: 30px;

    &__btn {
      width: 26px;
      height: 26px;
      margin: 0;
      padding: 0;
      border: none;
      background-color: transparent;
      background-image: url("../../img/catalog/save-empty.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100%;
    }

    &.active .card-favorite__btn {
      background-image: url("../../img/catalog/save-full.svg");
    }
  }

  &-sale {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 34px;
    padding: 0 12px;
    background: linear-gradient(45deg, #9B0006, #D30020);
    border-radius: 0 0 0 10px;

    &__text {
      @include fz(15px);
      color: $c-white;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
    }
  }

  &-preview {
    position: relative;
    margin: 0 0 22px;

    &__link img {
      width: 100%;
    }
  }

  &-name {
    padding: 0 20px;
    margin: 0 0 30px;

    @include mq(tablet) {
      padding: 0 30px;
    }

    &__link {
      text-decoration: none;

      &:hover .card-name__title {
        color: $c-link-hover;
      }
    }

    &__title {
      @include fz(26px);
      margin: 0 0 2px;
      color: $c-blue;
      font-family: $f-din-bold;
      letter-spacing: .3px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;

      @include mq(desktop) {
        @include fz(30px);
      }
    }

    &__subtitle {
      display: flex;
      align-items: center;
    }

    &__icon-wrap {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 13px;
      height: 16px;
      margin: 0 14px 0 0;
    }

    &__desc {
      @include fz(14px);
      color: $c-field-default;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

  &-info {
    padding: 0 20px;
    margin: 0 0 30px;

    @include mq(tablet) {
      padding: 0 30px;
    }

    &__text {
      @include fz(16px);
      margin: 30px 0 0;
      color: $c-dark;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
    }

    &-head {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0 0 30px;

      &__title {
        @include fz(30px);
        margin: 0;
        color: $c-blue;
        font-family: $f-din-bold;
        letter-spacing: .3px;
      }

      &__verified {
        display: flex;
        align-items: center;

        svg {
          width: 24px;
          height: 24px;
        }

        span {
          @include fz(15px);
          margin: 0 0 0 30px;
          color: $c-blue;
          font-family: $f-muller-medium;
          letter-spacing: .3px;
        }
      }
    }

    &_detail {
      margin: 0 0 80px;

      @include mq(tablet) {
        padding: 0;
      }
    }

    &__row {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &:last-child {
        margin: 16px 0 0;
      }

      &_detail {
        flex-wrap: wrap;
        justify-content: flex-start;
        margin: 0;
      }
    }

    &__item {
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 33%;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
      border-right: 1px solid rgba(187, 187, 187, .5);

      @include mq(tablet) {
        flex-direction: row;
        justify-content: center;
        align-items: flex-start;
      }

      &:last-child {
        border-right-color: transparent;
      }

      p {
        @include fz(14px);
        margin: 0 0 1px;
        color: $c-dark;

        @include mq(desktop, max) {
          text-align: center;
        }
      }

      span {
        @include fz(16px);
        display: block;
        color: $c-red;

        @include mq(desktop, max) {
          text-align: center;
        }
      }

      &_detail {
        width: 20%;
        margin: 0 0 30px;

        &:nth-child(5n + 5) {
          border-right-color: transparent;
        }
      }

      &_facilities {
        width: 16.6%;
        margin: 0;
      }
    }

    &__icon {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 24px;
      height: 24px;
      margin: 0 0 10px;


      @include mq(desktop) {
        margin: 0 10px 0 0;
      }

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }

  &-reviews {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &__col {
      width: 50%;
    }

    &-icons {
      display: flex;
      align-items: center;
      padding: 0;
      margin: 0;

      li {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 18px;
        height: 16px;
        list-style: none;

        & + li {
          margin: 0 0 0 10px;
        }

        svg {
          width: 100%;
          height: 100%;
        }
      }
    }

    &-link {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      text-decoration: none;

      @include mq(desktop) {
        align-items: center;
        flex-direction: row;
      }

      span {
        @include fz(12px);
        position: relative;
        top: 2px;
        color: $c-field-default;
        font-family: $f-muller-medium;
        letter-spacing: .3px;
      }

      &_detail {
        span {
          @include fz(18px);
          color: $c-dark;
        }
      }
    }
  }

  &-price {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &_detail {
      flex-direction: column;
      margin: 20px 0 30px;
    }

    &-wrap {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 20px;
      border-top: 1px solid rgba(187, 187, 187, .5);

      @include mq(tablet) {
        padding: 21px 30px 30px;
      }
    }

    &__new {
      @include fz(18px);
      margin: 0 10px 0 0;
      color: $c-red;
      font-family: $f-muller-medium;
      letter-spacing: .3px;

      span {
        @include fz(12px);
      }
    }

    &__old {
      display: none;

      @include mq(desktop) {
        @include fz(12px);
        position: relative;
        top: 2px;
        display: block;
        color: $c-field-default;
        font-family: $f-muller-medium;
        letter-spacing: .3px;
      }
    }
  }

  &-view-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-decoration: none;

    &_detail {
      display: inline-flex;
    }

    &_select {
      display: inline-flex;
      color: $c-red;
    }

    &__text {
      display: none;

      @include mq(desktop) {
        @include fz(15px);
        display: block;
        color: $c-blue;
        font-family: $f-muller-medium;
        letter-spacing: .3px;
      }

      &_red {
        @include mq(desktop) {
          color: $c-red;
        }
      }
    }

    &__icon {
      width: 30px;
      margin: 0 0 0 30px;

      svg {
        width: 100%;
        height: 100%;
        fill: $c-blue;
      }

      &_red svg {
        fill: $c-red;
      }
    }

    &:hover .card-view-link__text {
      color: $c-link-hover;
    }

    &:hover .card-view-link__icon svg {
      fill: #8daede;
    }
  }
}