.waves {
  position: absolute;
  z-index: 1;
  transform: translateX(-50%) scale(0);
  left: 50%;
  box-shadow: 0 5px 40px rgba(148, 183, 228, 0.6);
  border-radius: 10px;
  overflow: hidden;
  opacity: 0;
  transition: all .3s;

  &-wrap {
    margin: auto;
    max-height: 500px;

    img {
      margin: auto;
    }
  }

  &-1 {
    top: 235px;
    margin-left: -480px;
    width: 120px;
    height: 120px;
  }

  &-2 {
    top: 245px;
    margin-left: 670px;
    width: 170px;
    height: 170px;
  }

  &-3 {
    top: 480px;
    margin-left: -950px;
    width: 90px;
    height: 90px;
  }

  &-4 {
    top: 520px;
    margin-left: 540px;
    width: 90px;
    height: 90px;
  }

  &-5 {
    top: 600px;
    margin-left: -630px;
    width: 170px;
    height: 170px;
  }

  &-6 {
    top: 480px;
    margin-left: 950px;
    width: 90px;
    height: 90px;
  }
}

.visible {
  opacity: 1;
  transform: translateX(-50%) scale(1);
}