@font-face {
  font-family: 'MullerMedium';
  src: url('../fonts/MullerMedium/MullerMedium.woff2') format('woff2'),
  url('../fonts/MullerMedium/MullerMedium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "MullerBold";
  src: url("../fonts/MullerBold/MullerBold.woff") format("woff"),
  url("../fonts/MullerBold/MullerBold.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}


@font-face {
  font-family: 'DINProCondensedBold';
  src: url('../fonts/DINPro-CondensedBold/DINPro-CondensedBold.woff2') format('woff2'),
  url('../fonts/DINPro-CondensedBold/DINPro-CondensedBold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}