.more {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 10px;

  &_find {
    margin: 50px 0 0;
  }

  &__link {
    text-decoration: none;

    &:hover {
      .more__text {
        color: $c-link-hover;
      }

      .circle__btn_next {
        transform: translateX(16px);
      }

      .circle__btn {
        background: linear-gradient(45deg, #3c589e, #93b7e4);

        & svg {
          fill: $c-white;
        }
      }
    }
  }

  &__text {
    @include fz(15px);
    margin: 0 30px 0 0;
    font-family: $f-muller-medium;
    color: $c-blue;
    letter-spacing: .3px;

    &_white {
      color: $c-white;
    }
  }
}