.address {
  display: flex;
  flex-direction: column;
  font-style: normal;

  @include mq(tablet, max) {
    margin: 20px 0 0;
    text-align: center;
  }

  &_detail {
    @include mq(tablet, max) {
      margin: 0 0 30px;
    }
  }

  &__icn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    margin: 0 20px 0 0;

    svg {
      width: 100%;
      height: 100%;
    }

    &_detail {
      width: 16px;
      height: 16px;
    }
  }

  &__info {
    @include fz(16px);
    display: flex;
    align-items: center;
    margin: 4px 0;
    color: rgba(255, 255, 255, .5);
    font-family: $f-muller-medium;
    letter-spacing: .3px;
    text-decoration: none;

    &:not(:last-child) {
      margin: 0 0 30px;
    }

    &_link:hover {
      color: $c-white;
    }

    &_detail {
      color: $c-blue;
      margin: 0 auto;

      &:not(:last-child) {
        margin: 0 0 20px;
      }
    }
  }
}