.best {
  align-self: center;
  border-radius: 10px;
  background-color: $c-white;
  overflow: hidden;

  &:hover {
    @include mq(desktop) {
      .best-info__desc {
        max-height: 100px;
      }

      .best-info {
        @include mq(tablet) {
          padding: 24px 20px;
        }

        @include mq(desktop) {
          padding: 24px 30px;
        }
      }
    }
  }

  &_visible {
    .best-info__desc {
      max-height: 100px;
    }

    .best-info {
      @include mq(tablet) {
        padding: 24px 20px;
      }

      @include mq(desktop) {
        padding: 24px 30px;
      }
    }
  }
}