.footer-partner {
  display: flex;

  @include mq(tablet, max) {
    margin: 40px 0;
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }

  &__col:first-child {
    padding: 0 30px 0 0;
    border-right: 1px solid rgba(255, 255, 255, 0.2);
  }

  &__col:last-child {
    padding: 0 0 0 30px;
  }

  &__text {
    @include fz(14px);
    color: rgba(255, 255, 255, .5);
    font-family: $f-muller-medium;
    letter-spacing: .3px;
  }
}