.badge {
  position: absolute;
  bottom: 30px;
  left: 0;
  width: 160px;
  height: 40px;
  padding: 0 10px;
  border-radius: 0 20px 20px 0;
  background-color: rgba(255, 255, 255, .4);
  backdrop-filter: blur(30px) brightness(1.2);

  &_circle {
    width: 172px;

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 30px;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background-color: $c-white;
      transform: translateY(-50%);
    }
  }
}