.nav-list__link {
  @include fz(16px);
  color: $c-blue;
  font-family: $f-muller-medium;
  letter-spacing: .3px;
  text-decoration: none;
  text-transform: capitalize;

  &:hover {
    color: $c-link-hover;
  }

  &_main {
    color: rgba(255,255,255, .5);

    &:hover {
      color: rgba(255,255,255, 1);
    }
  }
}