.inspiration__link {
  position: relative;
  display: block;
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    height: 100%;
  }

  @media screen and (max-width: 767px) {
    img {
      width: 100%;
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    img {
      height: 100%;
    }
  }

  .play {
    position: absolute;
    top: 50%;
    left: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    transform: translate(-50%, -50%) rotate(-90deg);
    background-color: $c-white;
    box-shadow: 0 0 45px 15px #89BDFF, 0 0 0 22px rgba(255, 255, 255, .3);
  }
}

.mfp-iframe-holder .mfp-close {
  justify-content: flex-end;
  color: $c-blue;
}