.header {
  position: fixed;
  z-index: 4;
  top: 0;
  right: 0;
  left: 0;
  padding: 20px 0 0;
  background-color: $c-main-bg;
  transition: all .4s ease;

  @include mq(tablet) {
    margin: 0;
    padding: 30px 0 0;
    border-bottom: none;
  }

  @include mq(desktop) {
    position: fixed;
    z-index: 4;
    top: 0;
    right: 0;
    left: 0;
    padding: 40px 2%;
    margin: 0;
    border-bottom: none;
  }

  &.small-blue {
    background-color: rgba(8, 57, 111, .95);
    padding: 10px 0 0;

    @include mq(tablet) {
      padding: 10px 0 0;
    }

    @include mq(desktop) {
      padding: 10px 2%;
    }

    &::before {
      display: none;
    }

    .header__logo {
      width: 180px;
    }

    .adaptive-list {
      padding: 0 0 10px;
    }

    .header__logo_adaptive {
      @include mq(tablet, max) {
        width: 30px;
        height: 60px;
        overflow: hidden;
      }
    }
  }

  &.small-white {
    background-color: rgba(252, 252, 252, .95);

    @include mq(tablet) {
      padding: 10px 0 0;
    }

    @include mq(desktop) {
      padding: 10px 2%;
    }

    .header__logo {
      width: 180px;
    }
  }

  &_main {
    background-color: transparent;

    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 50%;
      width: 96%;
      max-width: 1640px;
      height: 1px;
      background-color: rgba(255, 255, 255, 0.4);
      transform: translateX(-50%);

      @include mq(tablet, max) {
        width: calc(100% - 40px);
      }
    }

    .white-logo {
      display: block;
    }
  }

  &__wrap {
    padding: env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left);
    margin: auto;
    max-width: 1640px;
  }

  &__top {
    margin: 0 20px;

    @include mq(desktop) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0;
    }
  }

  &__logo {
    display: none;

    @include mq(desktop) {
      display: block;
    }

    &_adaptive {
      display: block;

      @include mq(tablet, max) {
        width: 30px;
        height: 60px;
        overflow: hidden;

        img {
          max-width: none;
          height: 100%;
        }
      }

      @include mq(tablet) {
        height: 60px;

        img {
          height: 100%;
        }
      }
    }

    &_main .white-logo {
      @include mq(tablet) {
        display: block;
      }
    }
  }
}

.close-btn, .open-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  background: transparent;
  border: none;

  svg {
    width: 100%;
    height: 100%;
  }
}

.open-btn {
  width: 36px;
  height: 36px;
}

.close-btn {
  height: 36px;
  width: 36px;

  svg {
    width: auto;
    height: 80%;
  }
}

.adaptive-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0 0 20px;
  margin: 0;

  @include mq(tablet) {
    padding: 0 0 30px;
  }

  @include mq(desktop) {
    display: none;
  }

  &__item {
    list-style-type: none;

    &_btn-close {
      display: none;
    }

    &_logo {
      @include mq(tablet, max) {
        order: 1;
      }

      @include mq(tablet) {
        flex-grow: 1;
      }
    }

    &_btn-open {
      @include mq(tablet, max) {
        order: 0;
      }
    }

    &_compare {
      @include mq(tablet, max) {
        order: 2;
      }

      @include mq(tablet) {
        margin: 0 60px;
      }
    }

    &_dropdown {
      @include mq(tablet, max) {
        display: none;
      }
    }
  }
}
