.circle {
  position: relative;
  display: inline-flex;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgba(147, 182, 227, .3);

    @include mq(tablet) {
      width: 60px;
      height: 60px;
    }
  }

  &__btn {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 50%;
    background-color: #F3F3F3;
    cursor: pointer;
    overflow: hidden;
    transition: transform .2s;

    @include mq(tablet) {
      height: 60px;
      width: 60px;
    }

    svg {
      height: 16px;
      width: 10px;
      fill: #B6B6B6;
    }

    &:hover {
      background: linear-gradient(45deg, #3c589e, #93b7e4);

      svg {
        fill: $c-white;
      }
    }

    &_prev:hover {
      transform: translateX(-16px);
    }

    &_next:hover {
      transform: translateX(16px);
    }
  }
}