.filter-button {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 2px 0;
  background-color: transparent;
  border: none;

  &:after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 10px;
    height: 10px;
    background-image: url("../../img/svg/arrow-bottom.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    transform: translateY(-50%);
  }

  &.open::after {
    background-image: url("../../img/svg/arrow-top.svg");
  }
}