.footer__col_tablet {
  display: flex;

  @include mq(tablet, max) {
    flex-direction: column;
    margin: 0 0 40px;
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    position: relative;
    align-items: flex-end;
    justify-content: space-between;
  }

  @include mq(desktop) {
    flex-direction: column;
    justify-content: space-between;
    max-width: 234px;
    border-right: none;
  }
}