.team__avatar {
  @extend %avatar;
  width: 90px;
  height: 90px;

  @include mq(tablet) {
    width: 120px;
    height: 120px;
  }

  @include mq(desktop) {
    width: 150px;
    height: 150px;
  }

  &_detail {
    @include mq(desktop) {
      width: 100px;
      height: 100px;
    }
  }
}
