.section {
  padding: 0 0 62px;

  &_cloud {
    position: relative;
    overflow: hidden;
  }

  &_bd-top {
    position: relative;
    padding: 110px 0 62px;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 50%;
      width: 100%;
      max-width: 1300px;
      height: 1px;
      background-color: rgba(255,255,255, .4);
      transform: translateX(-50%);
    }
  }

  &_main {
    position: relative;
    height: 1080px;
    margin: 0 0 100px;
    background-color: $c-blue;
    background-image: url("../../img/index/banner2.jpg");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: unset;
    overflow: hidden;
  }

  &_waves {
    position: relative;
    padding: 0 0 100px;
    overflow-x: hidden;
  }
}
