.letter {
  margin: 0 0 52px;

  &-form {
    position: relative;
    max-width: 460px;
    margin: 0 auto;

    &__icon {
      position: absolute;
      top: 50%;
      left: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 24px;
      height: 24px;
      transform: translateY(-50%);
    }

    &__field {
      @include fz(14px);
      width: 100%;
      height: 50px;
      padding: 0 150px 0 70px;
      color: $c-white;
      font-family: $f-muller-medium;
      letter-spacing: .3px;
      border: 1px solid $c-main-bg;
      border-radius: 25px;
      background-color: $c-main-bg;

      &::-webkit-input-placeholder {
        @include fz(14px);
        color: #B9B9B9;
        font-family: $f-muller-medium;
        letter-spacing: .3px;
      }
      &::-moz-placeholder {
        @include fz(14px);
        color: #B9B9B9;
        font-family: $f-muller-medium;
        letter-spacing: .3px;
      }
      &:-ms-input-placeholder {
        @include fz(14px);
        color: #B9B9B9;
        font-family: $f-muller-medium;
        letter-spacing: .3px;
      }
      &:-moz-placeholder {
        @include fz(14px);
        color: #B9B9B9;
        font-family: $f-muller-medium;
        letter-spacing: .3px;
      }
    }

    & .button {
      position: absolute;
      right: 0;
      bottom: 0;
      height: 100%;

      &::before {
        border-radius: 0 25px 25px 0;
      }
    }
  }
}