.filter-form {
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    display: flex;
    justify-content: space-between;
    margin: 0 -20px;
  }

  &__box {
    @media screen and (min-width: 768px) and (max-width: 1199px) {
      width: calc(33.33% - 40px);
      margin: 0 20px;
    }

    @include mq(desktop) {
      padding: 0 0 26px;
    }

    &_popup {
      @media screen and (min-width: 768px) and (max-width: 1199px) {
        width: 100%;
        margin: 0;
      }
    }
  }
}

